<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="benfen">
           <img src="../assets/images/pic.png">
           <p>重要：立即备份您的账户</p>
        </div>
        <div class="notice">
          <h1>注意：</h1>
          <p>1、抄写助记词到你的本子上，并存放到安全的地方，千万不要放在网络上</p>
          <p>2、然后尝试转入，转出小额资产进行使用</p>
          <button type="button" @click="showpwd()">备份钱包</button>
          <!-- <yd-button size="large" type="primary" @click.native="show1 = true">备份钱包</yd-button> -->
        </div>
        <yd-popup v-model="show1" position="center" width="90%">
            <div class="dig">
                
                <p class="ttl">
                    请输入密码
                </p>
                <input type="password" name="">
                <p class="dig_bt">
                    <yd-button @click.native="show1 = false">取消</yd-button>
                    <yd-button @click.native="gowritezjc()">确认</yd-button>
                </p>
            </div>
        </yd-popup>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      show1:false,
      headerNavData:{
         left:true,
         title:'备份钱包',
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      }
    }
  },
  methods:{
    showpwd(){
      this.show1 = true
    },
    gowritezjc(){
      this.show1 = false
      this.$router.push({path:'/writezjc'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.benfen{
  padding: .5rem 0 .35rem;
  text-align: center;
  background: #2c94ed
}
.benfen img{
  display: block;
  width: 1rem;
  height: 1rem;
  margin: 0 auto .3rem;
}

.benfen p{
  text-align: center;
  line-height: 1.5;
  font-size: .3rem;
  color: #fff;
}
.notice{
  padding: .35rem .2rem;
}
.notice h1{
  font-weight: 100;
  font-size: .3rem;
  color: #c20919;
  margin-bottom: .3rem;
}
.notice p{
  font-size: .28rem;
  color: #333;
  line-height: 1.5;
  margin-bottom: .2rem
}
.notice button{
  background: #278eed;
  height: .7rem;
  line-height: .7rem;
  width: 100%;
  text-align: center;
  color: #fff;
  border:none;
  font-size: .3rem;
  border-radius: 5px;
  margin: .4rem 0
}
.notice button:active{
  background: #3785cd;
}
.dig{
  background: #fff;
  padding: .4rem;
  text-align: center;
}
.ttl{
  font-size: .3rem;
  color: #278eed;
}
.dig input{
  width: 100%;
  height: .7rem;
  line-height: .7rem;
  padding: 0 .12rem;
  background: #f4f4f4;
  border:none;
  border-radius: 5px;
  color: #999;
  font-size: .28rem;
  margin: .3rem 0;
}
.dig_bt button{
  margin: 0 .3rem;
  padding: 0 .5rem
}
.dig_bt button:nth-child(1){
  background: #c20919;
}
.dig_bt button:nth-child(2){
  background: #278eed;
}
</style>



